.sidebar {
  nav {
    z-index: 2;
    transition: all 1s ease;

    button {
      display: none;
    }
  }
}

@media screen and (max-width: 990px) {
  .sidebar {
    nav {
      button {
        display: block;
      }

      &.open {
        height: 100vh;
        background-color: black !important;
      }
    }

    .items {
      transition: all 1s ease;
      display: none;

      &.open {
        display: flex;
      }
    }
  }
}
